* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
}

.goal-item {
  margin: 1rem 0;
  background: #8b005d;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  color: white;
  padding: 1rem 2rem;
  cursor: pointer;
}

.goal-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.Button_button__2BJsp {
  width: 100%;
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #8b005d;
  color: white;
  background: #8b005d;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
  cursor: pointer;
}

.Button_button__2BJsp:focus {
  outline: none;
}

.Button_button__2BJsp:hover,
.Button_button__2BJsp:active {
  background: #ac0e77;
  border-color: #ac0e77;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
}

@media (min-width: 768px) {
  .Button_button__2BJsp {
    width: auto;
  }
}

.CourseInput_form-control__2rMq7 {
  margin: 0.5rem 0;
}

.CourseInput_form-control__2rMq7 label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.CourseInput_form-control__2rMq7 input {
  display: block;
  width: 100%;
  border: 1px solid #ccc;
  font: inherit;
  line-height: 1.5rem;
  padding: 0 0.25rem;
}

.CourseInput_form-control__2rMq7 input:focus {
  outline: none;
  background: #fad0ec;
  border-color: #8b005d;
}

.CourseInput_form-control__2rMq7.CourseInput_invalid__2vDDu input {
  border-color: red;
  background: #ffd7d7;
}

.CourseInput_form-control__2rMq7.CourseInput_invalid__2vDDu label {
  color: red;
}

#goals {
  width: 35rem;
  max-width: 90%;
  margin: 3rem auto;
}

#goal-form {
  width: 30rem;
  max-width: 90%;
  margin: 3rem auto;
  padding: 2rem;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

